@charset "UTF-8";

// Warning alert.
.su-alert--warning {
  background-color: map-get($alert-colors, 'warning');
  background-image: url("#{$su-image-path}/alert-warning.png");
  background-image: url("#{$su-image-path}/alert-warning.svg");

  a {
    color: $color-cardinal-red;

    &:hover {
      color: $color-black;
    }
  }
}
